@import url("https://fonts.googleapis.com/css2?family=PT+Mono&family=PT+Sans:ital@1&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* font-family: "PT Sans", sans-serif; */
  font-family: "PT Mono", monospace;
}

.info {
  display: grid;
  text-align: center;
  justify-content: center;
  margin-top: 20px;
  grid-template-columns: 400px;
  grid-template-rows: 160px 80px;
  align-items: center;
}

body {
  background-image: linear-gradient(rgb(30, 201, 93), rgb(131, 131, 46));
}
/* 
.info div {
  border: 2px solid black;
} */
.devImg {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  border: solid #000000 1px;
}

/* .links-main-con {
  border: solid black 1px;
} */

.links-con {
  /* border: solid rgb(250, 17, 17) 1px; */
  display: grid;
  grid-template-columns: 600px;
  justify-content: center;
  gap: 10px;
  padding-top: 50px;
  padding-bottom: 50px;
}
.link {
  /* border: solid rgb(250, 17, 17) 1px; */
  border-radius: 4px;
  background-color: #ffffff;
}

h1 {
  color: white;
}

.link-con-c {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 180px;
  align-items: center;
}

#icon {
  height: 50px;
  border-radius: 50%;
}

#linkStyle {
  text-decoration: none;
  color: #52c241;
}
